import React from 'react';

export default class Contents extends React.Component {
  constructor (props) {
    super (props);
    this.state = {
      contents: false,
    };
  };

  handleClick () {
    this.setState({
      contents: !this.state.contents,
    });
  };

  render () {
    return (
      <div class="nes-container with-title "onClick={() => this.handleClick()}>
        {this.state.contents ? <div>Contents:clicked</div>: <div>Contents:click</div>}
        <ul class="nes-list is-disc">
          {this.state.contents ? <li><a href="https://github.com/ritarock">github</a></li> : <div>&nbsp;</div>}
          {this.state.contents ? <li><a href="http://ritarock.hatenablog.com/">hatena blog</a></li> : <div>&nbsp;</div>}
          {this.state.contents ? <li><a href="https://qiita.com/ritarock">qiita</a></li> : <div>&nbsp;</div>}
          {this.state.contents ? <li><a href="https://booklog.jp/users/ritarock">booklog</a></li> : <div>&nbsp;</div>}
        </ul>
      </div>
    );
  }
}

