import React from 'react';

export default class Who extends React.Component {
  constructor (props) {
    super (props);
    this.state = {
      name: "rita",
      mail: "wkryz1204@gmail.com",
      who: false,
    };
  };

  handleClick () {
    this.setState({
      who: !this.state.who,
    });
  };

  render () {
    return (
      <div class="nes-container with-title" onClick={() => this.handleClick()}>
        {this.state.who ? <div>Who:clicked</div> : <div>Who:click</div>}
        <ul class="nes-list is-disc">
          {this.state.who ? <li>Name: {this.state.name}</li> : <div>&nbsp;</div>}
          {this.state.who ? <li>Mail: {this.state.mail}</li> : <div>&nbsp;</div>}
        </ul>
      </div>
    );
  }
}

